@import url(~normalize.css);
@import url(~@blueprintjs/core/lib/css/blueprint.css);
@import url(~@blueprintjs/icons/lib/css/blueprint-icons.css);
body {
  margin: 0;
  color: #137cbd;
  background-color: #182026;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.Main {
  display: flex;
  flex-direction: column;
  align-items: center; }
  .Main h1 {
    color: white; }
  .Main .Inputs {
    display: flex;
    flex-direction: column;
    width: 50%;
    align-items: center; }
    .Main .Inputs .Number-Inputs {
      display: flex;
      justify-content: space-around;
      width: 100%; }
      .Main .Inputs .Number-Inputs .number-input {
        width: 10em; }
      .Main .Inputs .Number-Inputs label {
        color: #137cbd;
        text-align: center; }
  .Main .board-sprite {
    display: grid;
    grid-template-columns: 30% 70%; }
    .Main .board-sprite .sprite-preview {
      text-align: center;
      flex-direction: column;
      justify-content: center;
      display: flex;
      align-items: center;
      align-content: center; }
      .Main .board-sprite .sprite-preview img {
        background-color: red; }
    .Main .board-sprite .Board {
      display: grid;
      align-self: start;
      justify-content: left; }
      .Main .board-sprite .Board .square {
        background-color: #202b33;
        border: solid #293742 0.01em;
        cursor: pointer;
        color: transparent; }
      .Main .board-sprite .Board .sprite {
        background-color: #30404d;
        border: solid #293742 0.01em;
        z-index: 2;
        color: transparent;
        cursor: arrow; }
  .Main .Sprites-container {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    padding: 0 5% 0 5%;
    justify-content: space-around; }
    .Main .Sprites-container label {
      color: #137cbd;
      text-align: center; }
    .Main .Sprites-container .Sprite-details {
      display: flex;
      width: 10%;
      align-items: center;
      flex-direction: column; }
      .Main .Sprites-container .Sprite-details .sprite-inputs {
        display: flex;
        align-items: center; }
        .Main .Sprites-container .Sprite-details .sprite-inputs .sprite-size {
          width: 80%;
          margin-left: 10%; }

h4 {
  color: red; }
